<template>
    <div style="overflow: hidden">

        <div id="mybody">
            <img id="tophand" src="../img/shou5.png" />
            <img id="mypig" src="../img/pig.png" />
            <img id="regis" src="../img/word2.png"/>
            <Topbar />
            <div class="block">

                <el-input prefix-icon="el-icon-user" v-model="name" style="font-size: 15px;" placeholder="Please enter your name"></el-input>

                <el-input prefix-icon="el-icon-bank-card" v-model="id" style="font-size: 15px;" placeholder="Please enter your ID number"></el-input>

                <el-input prefix-icon="el-icon-mobile" v-model="phone" style="font-size: 15px;" placeholder="Please enter your phone number"></el-input>

                <el-input prefix-icon="el-icon-s-promotion" v-model="email" style="font-size: 15px;" placeholder="Please enter your email"></el-input>

                <el-input prefix-icon="el-icon-lock" v-model="password" style="font-size: 15px;" placeholder="Please enter your password" show-password></el-input>

                <button @click="register" class="btn btn-default" style="font-size:15px;width:100%;">
                    Register
                </button>
            </div>

        </div>

    </div>

    <!--  <div v-else>Loading...</div>-->


</template>

<script>
    import { mapGetters } from 'vuex'
    import Topbar from "@/components/topbar";

    import {App} from '../js/Test';
    import home from '@/components/home'
    import Web3 from "web3";
    export default {
        name: "register",
        components: {Topbar},
        computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data(){
            return{
                name: '',
                id:'',
                email:'',
                password:'',
                phone:'',
                imageUrl:'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',

            }
        },
        methods:{
            register(){
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                let thisname=this.name;
                App.createUser(this.name,this.id,this.phone,this.email,this.password).then(result => {
                    console.log(result);
                    if (result) {
                        this.$router.push({
                            path:'/center',
                            params:{
                                centerusername:thisname,
                            },
                            name:'hey',
                        });
                    }
                });

            }

        },
        created(){
            if(localStorage.getItem('touxiang')){
                this.imageUrl=localStorage.getItem('touxiang');
            }

        },

    }
</script>

<style scoped>

    #mypig{position:absolute;top:70vh;left: 25vw;width:20vw;height:30vh;}
    #tophand{position:absolute;top:0;left: 0;width:65vw;height:65vh;}
    #mybody{
        font-size: 15px;

        background: url("../img/bg1.png") no-repeat;
        background-size: 100% 100vh;
        font-size: 15px;
        overflow: hidden;position:absolute;top:0;
        width:100%;
        height:100vh;
    }

    #regis{position:absolute;top:10%;left:60%;width:30%;height:16%;}
    .block{
        line-height: 10vh;
        position: absolute;
        display: flex;
        top:32vh;
        left:60vw;
        width:29vw;
        height:50vh;
        flex-direction: column;
        align-items: flex-start;
    }

</style>

