<template>

    <div class="mybody">

        <Topbar/>
        <img src="../img/title.png" style="width:25vw;height:25vh;margin-left:37vw;float:left;margin-top: 10vh;"/>
        <img src="../img/coin.png" id="coin" />
        <img src="../img/hand-pig.png" id="handpig"/>

        <div class="block">

            <el-carousel height="40vh" style="width:35vw;">
                <el-carousel-item v-for="(list,index) in Mlist" :key="index" >
                    <img alt="" :src="list.images" />
                </el-carousel-item>
            </el-carousel>
            <div class="project-description">
                <div class="project-name">{{project.projectName}}</div>
                <div class="project-owner">项目发起人：{{project.owner}} </div>
                <div class="project-country">所在国家：{{project.country}}</div>
                <div class="project-startDate">开始时间：{{new Date(project.startDate * 1000).toDateString()}} </div>
                <div class="project-end" v-if="project.end">状态：已完结</div>
                <div class="project-end" v-else>状态：未完结</div>
                <div class="project-phoneNum">联系方式：{{project.phoneNum}}</div>
                <div class="project-like">点赞数：{{project.tokenSum}}</div>
                <div class="project-btn">
                    <div class="like">
                        <img v-if="project.hasDonate" src="../img/like.png" @click="donate_drawback" width="40" height="40" />
                        <img v-else src="../img/like_black.png" @click="donate" width="40" height="40" />
                    </div>
                    <div class="stars">
                        <img v-if="hasFavor" src="../img/star.png" @click="favor_drawback" width="30" height="30"/>
                        <img v-else src="../img/star_black.png" @click="favor" width="30" height="30"/>
                    </div>
                </div>

            </div>

        </div>

        <div class="process" >
            <div class="processtitle">项目详情</div>
            <div class="infos">
                {{ project.projectDetail}}
            </div>
        </div>
    </div>
    <!--  <div v-else>Loading...</div>-->
</template>

<script>

    // import {mapGetters} from "vuex";
    import Topbar from "@/components/topbar";
    import {App} from '../js/Test';
    import Web3 from "web3";
    export default {
        name: "projectDetail",
        components: {Topbar},
        // computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data(){
            return{
                input:'',
                numbers:0,
                project:{
                    'id': null,
                    'tokenSum': null,
                    'type': null,
                    'country': null,
                    'startDate': null,
                    'end': false,
                    'projectName': null,
                    'projectDetail': null,
                    'projectType': null,
                    'owner': null,
                    'phoneNum': null,
                    'hasDonate': null,
                },
                Mlist:[
                    {
                        id:1,
                        images:require("../img/lun1.jpg")
                    },
                    {
                        id:2,
                        images:require("../img/lun2.jpg")
                    },
                    {
                        id:3,
                        images:require("../img/lun3.jpg")
                    },
                    {
                        id:4,
                        images:require("../img/lun4.jpg")
                    }
                ],
                dialogTableVisible: false,
                dialogFormVisible: false,
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                formLabelWidth: '120px',
                favorProjects: [],
            }
        },
        computed: {
            hasFavor: function () {
                return this.favorProjects.includes(this.$route.query.id);
            }
        },
        methods: {
            back() {
                this.$router.go(-1);
            },

            handleClose() {
                this.$confirm('Do you really want to cancel？')

            },
            dialogVisible(){
                this.dialogFormVisible=true;
                this.dialogTableVisible=true;
            },

            async getFavorProjects() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getAllFavorProjects().then(res => {
                    this.favorProjects = res;
                    console.log('favor projects: ', res);
                })
            },

            async getProject(id) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getProject(id).then(project => {
                    this.project.tokenSum = project[0];
                    this.project.type = project[1];
                    this.project.country = project[2];
                    this.project.startDate = project[3];
                    this.project.end = project[4];
                });
                App.getProjectDetail(id).then(project => {
                    this.project.projectName = project[0];
                    this.project.projectDetail = project[1];
                    this.project.projectType = project[2];
                    let ownerAddress = project[3];
                    App.findUser(ownerAddress).then(user => {
                        this.project.owner = user['username'];
                        this.project.phoneNum = user['phoneNumber'];
                    });
                });
                App.hasDonated(id).then(res => {
                    this.project.hasDonate = !res;
                });
            },

            async donate() {
                let id = this.$route.query.id;
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.donate(id).then(res => {
                    console.log(res);
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '点赞成功',
                            type: 'success'
                        });
                        // this.updateTypeList(type);
                        this.project.hasDonate = true;
                        this.project.tokenSum++;
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '点赞失败'
                        })
                    }
                });
            },

            async donate_drawback() {
                let id = this.$route.query.id;
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.donateDrawBack(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '取消点赞成功',
                            type: 'success'
                        });
                        // this.updateTypeList(type);
                        this.project.hasDonate = false;
                        this.project.tokenSum--;
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '取消点赞失败'
                        })
                    }
                });
            },

            async favor() {
                let id = this.$route.query.id;
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.addFavorProject(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '收藏成功',
                            type: 'success'
                        });
                        this.getFavorProjects();
                        this.favorProjects.push(this.$route.query.id);
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '收藏失败'
                        })
                    }
                });
            },

            async favor_drawback() {
                let id = this.$route.query.id;
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.deleteFavorProject(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '取消收藏成功',
                            type: 'success'
                        });
                        this.getFavorProjects();
                        this.favorProjects.splice(this.project.indexOf(this.$route.query.id), 1);
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '取消收藏失败'
                        })
                    }
                });
            }
        },
        created(){
            console.log(this.$route.query.id);
            this.getFavorProjects();
            this.getProject(this.$route.query.id)
        }

    }
</script>

<style scoped>
    .processtitle{
        /*float: left;*/
        /*margin-left: 47%;*/
        margin-top:2vh;
        font-size:20px;
        border: 1px none black;
        border-bottom-style: solid;
    }
    .infos{
        text-align: justify;
        font-size: 17px;
        margin: 30px 30px 30px 30px;
    }
    #handpig{/*margin-left:-124%;margin-top:30%;*/position: absolute;left:0;top:90vh;width:35vw;height:60vh;}
    #coin{position: absolute;z-index: 1;right:0;top:0;width:20vw;height:30vh}
    .illus{
        height:25vh;
        line-height: 10vh;
        text-align: left;
        font-size: 17px;

    }

    .project-name {
        font-size: xx-large;
    }


    .mybody{
        background: url("../img/bg1.png") no-repeat;
        background-size: 100% 150vh;
        position:absolute;
        width:100%;height: 150vh;
        top:0;
        overflow: hidden;
    }
    .block{
        position: absolute;
        top:40vh;
        left:8vw;
        height:40vh;
        width:80vw;
        display: flex;
        flex-direction: row;
    }
    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 150px;
        margin: 0;
    }

    .el-carousel__item:nth-child(2n) {
        background-color: #99a9bf;
    }

    .el-carousel__item:nth-child(2n+1) {
        background-color: #d3dce6;
    }

    .project-description {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        width:40vw;
        height:40vh;
        padding-left: 3vw;
        padding-bottom: 2vh;
    }

    .project-btn {
        flex-direction: row;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .like{
        margin-right: 20px;
    }
    .like:hover{
        cursor: pointer;
    }
    .stars:hover{
        cursor:pointer;
    }
    .process{
        position:absolute;
        top:90vh;
        left:40vw;
        width:50vw;
        height:50vh;
        border-radius: 10px;
        border: solid 1px black;
        overflow: hidden;
        text-align: center;
    }

</style>
