<template>
    <div class="mybody">

        <div class="topbar">
            <img src="../img/title.png" width="200" height="100" style="position:fixed;right:550px;margin-top: 0;"/>
        </div>
        <div class="search-area">
            <el-input placeholder="请输入内容" v-model="input" class="input-with-select">
                <el-select v-model="select" slot="prepend" placeholder="请选择" class="select-area">
                    <el-option label="项目名称" value="1" />
                    <el-option label="项目发起人" value="2" />
                    <el-option label="国家/地区" value="3" />
                </el-select>
                <el-button slot="append" icon="el-icon-search" @click="searchProjects" />
            </el-input>
            <el-card class="project-list-card">
                <el-scrollbar>
                    <el-table :data="results" stripe>
                        <el-table-column prop="projectName" label="名称" />
                        <el-table-column prop="owner" label="发起人" />
                        <el-table-column prop="type" label="类型" />
                        <el-table-column prop="country" label="国家" />
                        <el-table-column prop="startDate" :formatter="formatDate" label="开始时间" />
                        <el-table-column prop="end" :formatter="formatBoolean" label="是否完结" />
                        <el-table-column label="查看详情">
                            <template slot-scope="scope">
                                <el-button type="info" size="small" plain icon="el-icon-info" @click="click_item(scope.row)">详情</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-scrollbar>
            </el-card>
        </div>
        <el-menu style="position: fixed;top:0px;left:0;background-color:rgba(255,255,255,0)" class="el-menu-demo" mode="horizontal">
            <el-menu-item index="1"><router-link to="/home">首页</router-link></el-menu-item>
        </el-menu>
        <img src="../img/searchrst/searchrst_Hand1.png" width="250" height="150" style="position:fixed;right:0px;top: 0;"/>
        <img src="../img/searchrst/searchrst_hand2.png" width="225" height="200" style="position:fixed;left:0px;bottom: 0;"/>

    </div>
    <!--    <div v-else>Loading...</div>-->
</template>

<script>
    // import { mapGetters } from 'vuex'
    import {App} from '../js/Test';
    import Web3 from "web3";
    export default {
        name: "searchrst",
        // computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data(){
            return{
                select: '',
                input: '',
                projects: [],
                results: [],
                donateNum: [],
                hasDonate: []
            }
        },
        methods: {
            async getProjectEachType(type) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                let projects = [];
                App.getProjectEachType(type).then(nums => {
                    console.log(nums);
                    nums.forEach(num => {
                        let tmp = {
                            'id': 0,
                            'tokenSum': 0,
                            'type': '',
                            'country': '',
                            'startDate': 0,
                            'end': false,
                            'projectName': '',
                            'projectDetail': '',
                            'projectType': '',
                            'owner': '',
                            'hasDonate': false,
                        };
                        console.log(num);
                        tmp.id = num;
                        App.getProject(num).then(project => {
                            tmp.tokenSum = project[0];
                            tmp.type = project[1];
                            tmp.country = project[2];
                            tmp.startDate = project[3];
                            tmp.end = project[4];
                            this.donateNum[num] = project[0];
                        });
                        App.getProjectDetail(num).then(project => {
                            tmp.projectName = project[0];
                            tmp.projectDetail = project[1];
                            tmp.projectType = project[2];
                            let ownerAddress = project[3];
                            console.log('Address type:', typeof(ownerAddress));
                            App.findUser(ownerAddress).then(user => {
                                console.log(user['username']);
                                tmp.owner = user['username'];
                            });
                        });
                        App.hasDonated(num).then(res => {
                            tmp.hasDonate = !res;
                            this.hasDonate[num] = !res;
                        });
                        projects.push(tmp);
                    });
                }).then(() => {
                    this.projects = projects;
                    this.results = projects;
                    console.log(projects);
                });
            },
            formatDate(row, column, cellValue) {
                return new Date(cellValue * 1000).toDateString();
            },
            formatBoolean(row, column, cellValue) {
                let ret = '';
                if (cellValue) ret = '已完结';
                else ret = '未完结';
                return ret;
            },
            click_item(row) {
                console.log('row is: ', row);
                this.$router.push({
                    path: "/prodetail",
                    query: {id:row.id},
                });
            },
            searchProjects() {
                let projects = this.projects;
                let results;
                if (this.input === '') {
                    console.log('input is null');
                    results = projects;
                }
                else {
                    console.log('input is ', this.input);
                    results = projects.filter(this.createFilter());
                }
                console.log(results);
                this.results = results;
            },
            createFilter() {
                switch(this.select) {
                    case '1':
                        return (project) => {
                            return (project.projectName.toLowerCase().indexOf(this.input.toLowerCase()) === 0);
                        };
                    case '2':
                        return (project) => {
                            return (project.owner.toLowerCase().indexOf(this.input.toLowerCase()) === 0);
                        };
                    case '3':
                        return (project) => {
                            return (project.country.toLowerCase().indexOf(this.input.toLowerCase()) === 0);
                        };
                    default:
                        return (project) => {
                            return (project.projectName.toLowerCase().indexOf(this.input.toLowerCase()) === 0);
                        };
                }
            }
        },
        mounted() {
            this.getProjectEachType('all')
        }
    }


</script>

<style scoped>
    .mybody{

        background: url("../img/searchrst/serachrst_bg1.png") no-repeat;
        background-size:cover;
        width:100%;
        height:100%;/*宽高100%是为了图片铺满屏幕 */
        z-index:-1;
        position: absolute;
        overflow: hidden;
        background-attachment:fixed;
        background-position : center 0;
    }

    .block{
        position: absolute;
        top:150px;
        left:850px;
        width:400px;
        height:500px;
    }

    .search-area{
        margin: 20vh 20% 20px 10%;
        display:flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .input-with-select {
        width: 60%;
    }

    .select-area {
        background-color: #fff;
        width: 150px;
        height: 38px;
    }

    .input-with-select .el-input-group__prepend {

    }

    .project-list-card {
        margin-top: 15px;
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        /*left:100px;*/
        /*top:20px;*/
        /*width:1100px*/
    }

    .el-scrollbar {
        height: 100%;
    }

    .el-scrollbar__wrap {
        overflow: scroll;
        overflow-x: hidden;
    }
</style>

