// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App.vue';
import ElementUI from 'element-ui'
import locale from 'element-ui/lib/locale/lang/zh-CN'
import 'element-ui/lib/theme-chalk/index.css'
import './css/bootstrap.min.css'
// import Vuex from 'vuex'


import './utils/flexible'
import store from './js/store'


Vue.config.productionTip = false;
Vue.use(ElementUI, {locale});
import VueRouter from 'vue-router';
Vue.use(VueRouter);
// Todo: Update this when publishing
// import drizzleVuePlugin from '@drizzle/vue-plugin'
//
import drizzleVuePlugin from '@drizzle/vue-plugin'
import drizzleOptions from './drizzleOptions'



// 引入组件
import home from './components/home.vue';
import register from  './components/register.vue';

import createProject from "./components/createProject.vue";
import login from './components/login.vue';
import center from './components/center.vue';
import searchrst from './components/searchrst.vue';
import projectDetail from "@/components/projectDetail.vue";
//定义路径
const routes = [
  { path: '/home', component: home ,name:'hey'},
  { path: '/register', component: register },
  { path: '/', component: home},
  { path: '/createProject',component:createProject },
  { path: '/login', component: login },
  { path: '/center', component: center, name:'toCenter',},
  { path: '/searchrst', component: searchrst },
  { path:'/prodetail',component: projectDetail }
];
//实例化VueRouter
const router = new VueRouter({
  routes
});


Vue.use(drizzleVuePlugin, { store, drizzleOptions });

Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
}).$mount('#app');

