<template>
    <div class="body">
        <Topbar />
        <img class="hand3" src="../img/hand3.png"/>
        <img class="twohands" src="../img/twohands.png"/>
        <img class="hand6" src="../img/hand6.png"/>


        <div class="ins">
            <div class="up">
                <el-form-item label="类型"></el-form-item>
                <el-select v-model="projectType" style="width:140px;">
                    <el-option label="entertainment" value="entertainment"></el-option>
                    <el-option label="art" value="art"></el-option>
                    <el-option label="education" value="education"></el-option>
                    <el-option label="design" value="design"></el-option>
                    <el-option label="music" value="music"></el-option>
                </el-select>
                <foreign-area  style="width:30%;" :popularCity=cities selectBg="" v-on:func="selectCountry" />
                <el-input style="width:40%;" v-model="projectName" placeholder="Project Name" />
                <!--        <el-date-picker style="width:30%;"-->
                <!--          v-model="value1"-->
                <!--          type="datetime"-->
                <!--          placeholder="选择日期时间">-->
                <!--        </el-date-picker>-->
                <el-button plain style="width:30%;" @click="createPro">submit</el-button>
            </div>

            <div class="down">
                <textarea class="text" v-model="projectDetail" placeholder="Enter Project detail..."></textarea>
            </div>

        </div>
    </div>
    <!--  <div v-else>Loading...</div>-->
</template>

<script>
    import foreignArea from "./foreignArea.vue" ;
    import { mapGetters } from 'vuex'
    import Topbar from "@/components/topbar";
    import {App} from '../js/Test';
    import Web3 from "web3";
    export default {

        name: "createProject",
        computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data(){
            return{
                projectType: 'design',
                country: '',
                projectName:'',
                value: '',
                imageUrl:'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
                pickerOptions: {
                    disabledDate(time) {
                        return time.getTime() > Date.now();
                    },
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },
                value1: '',
                value2: '',
                projectDetail:'',
                cities: { value: "China", label: "中国" },
            }
        },
        created(){
            this.imageUrl=localStorage.getItem('touxiang');
        },
        components:{Topbar, 'foreign-area':foreignArea},
        methods:{
            selectCountry() {
                console.log(arguments[0]);
                this.country = arguments[0];
            },

            go_home() {
                this.$router.push({
                    path: "/home",
                });
            },

            createPro(){
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                console.log(this.projectDetail);
                console.log(this.country);

                App.createProject(this.projectName, this.projectType,this.country,this.projectDetail)
                    .then(res => {
                        console.log(res);
                        if (res) {
                            this.$notify({
                                title: '成功',
                                message: '项目创建成功',
                                type: 'success'
                            })
                        }
                        else {
                            this.$notify.error({
                                title: '失败',
                                message: '项目创建失败',
                            })
                        }
                        setTimeout(() => {
                            this.go_home()
                        }, 1000);
                    });
                //this.$router.go(0);
            }
        }

    }

</script>

<style scoped>
    .text{font-size:14px;padding:16px;width:60vw;border-color: white;border-radius: 5px;margin-top:2vh;height:40vh;}
    .hand6{position: absolute;top:21vw;left:0;width:40vw;height:55vh;}
    .hand3{position: absolute;top:0;left:0;width:70vw;height:80vh}
    .twohands{margin-top: 0;margin-right: 0;float: right;width:30vw;height:40vh;}
    .body{

        background: url("../img/stone.png") no-repeat;
        background-size: 100% 100vh;
        overflow: hidden;position:absolute;top:0;
        width:100%;
        height:100vh;
    }
    .ins{
        border-radius: 3px;
        position: absolute;
        top:40vh;
        left:35vw;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width:60vw;
        height:70vh;
    }
    .up{
        display: flex;
        flex-direction: row;
        align-items: flex-start;
    }
    .down{
        width:60vw;
    }
    textarea::-webkit-input-placeholder{
        color:#C4C4CC;
    }

</style>
