<template>
    <div class="mybody">
        <TopBar :thissrc="imageUrl" />
        <div class="Avatar">
            <!--        <el-avatar :src=imageUrl :size="120" shape="square" fit="scale-down" @click="uploadAvatar"/>-->
            <el-upload
                    class="avatar-uploader"
                    action=""
                    :http-request="uploadHttp"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload">
                <el-image v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"/>
            </el-upload>
        </div>

        <div class='info'>
            <el-tabs type="border-card" style="position:absolute;height:500px;width:100%;overflow:auto">
                <el-tab-pane>
                    <span slot="label">个人信息</span>
                    <div>
                        <el-card class="user-info-card" v-if="isExistUser">
                            <div class="text item">
                                用户昵称：{{user.userName}}
                            </div>
                            <div class="text item">
                                创建时间：{{new Date(user.time * 1000).toDateString()}}
                            </div>
                            <div class="text item">
                                ID Number: {{user.idCard}}
                            </div>
                            <div class="text item">
                                电话号码：{{user.phoneNumber}}
                            </div>
                            <div class="text item">
                                邮箱：{{user.email}}
                            </div>
                            <div class="text item">
                                token余额：{{user.balance}}
                            </div>
                        </el-card>
                        <el-card class="user-info-card" v-else>
                            <p>请先登录</p>
                        </el-card>
                    </div>
                </el-tab-pane>
                <el-tab-pane label="创建项目">
                    <div class="table-btn">
                        <el-button type="primary" @click="endSelectedProjects()">
                            完结项目
                        </el-button>
                    </div>
                    <el-table
                        :data="ownedProjects"
                        style="width:100%"
                        stripe
                        :row-class-name="tableRowClassName1"
                        @selection-change="handleSelectionChange">
                        <el-table-column type="selection" width="55" />
                        <el-table-column prop="projectName" label="名称" />
                        <el-table-column prop="type" label="类型" />
                        <el-table-column prop="country" label="国家" />
                        <el-table-column prop="startDate" :formatter="formatDate" label="开始时间" />
                        <el-table-column prop="end" :formatter="formatBoolean" label="是否完结" />
                        <el-table-column label="查看详情">
                            <template slot-scope="scope">
                                <el-button type="info" size="small" plain icon="el-icon-info" @click="click_item(scope.row)">详情</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="点赞项目">
                    <el-table
                            :data="donateProjects"
                            style="width:100%"
                            stripe>
                        <el-table-column prop="projectName" label="名称" />
                        <el-table-column prop="owner" label="发起人" />
                        <el-table-column prop="type" label="类型" />
                        <el-table-column prop="country" label="国家" />
                        <el-table-column prop="startDate" :formatter="formatDate" label="开始时间" />
                        <el-table-column prop="end" :formatter="formatBoolean" label="是否完结" />
                        <el-table-column label="查看详情">
                            <template slot-scope="scope">
                                <el-button type="info" size="small" plain icon="el-icon-info" @click="click_item(scope.row)">详情</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="收藏项目">
                    <el-table
                            :data="favorProjects"
                            style="width:100%"
                            stripe>
                        <el-table-column prop="projectName" label="名称" />
                        <el-table-column prop="owner" label="发起人" />
                        <el-table-column prop="type" label="类型" />
                        <el-table-column prop="country" label="国家" />
                        <el-table-column prop="startDate" :formatter="formatDate" label="开始时间" />
                        <el-table-column prop="end" :formatter="formatBoolean" label="是否完结" />
                        <el-table-column label="查看详情">
                            <template slot-scope="scope">
                                <el-button type="info" size="small" plain icon="el-icon-info" @click="click_item(scope.row)">详情</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </div>
    </div>
    <!--    <div v-else>Loading...</div>-->

</template>

<script>
    // import { mapGetters } from 'vuex'
    import TopBar from './topbar'
    import ossClient from '../js/aliyun.oss.client';
    import {App} from "../js/Test";
    import Web3 from "web3";
    // import {App} from '../js/Test'

    export default {
        components: {TopBar},
        // computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data () {
            return {
                sizeList: "large",
                imageUrl:'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
                // imageUrl: 'https://maples31-blog.oss-cn-beijing.aliyuncs.com/img/20210203222705.png',
                uploadConf: {
                    bucket: 'citi-cup',
                    region: 'oss-cn-beijing',
                    accessKeyId: 'LTAI4FywQBAQUDKdkwEEegDL',
                    accessKeySecret: 'mAzVeHEYjrMuPHYmxX0o7PjHaxdG7q'
                },
                isExistUser: false,
                user: {
                    userAddress: null,
                    userName: null,
                    time: null,
                    index: null,
                    idCard: null,
                    phoneNumber: null,
                    email: null,
                    balance: null
                },
                ownedProjects: [],
                favorProjects: [],
                donateProjects: [],
                selectedProjects1: []
            }
        },
        computed: {
            state() {
                let arr = [];
                for (let i = 0; i < this.ownedProjects.length; i++) {
                    let project = this.ownedProjects[i];
                    if (project.end) {
                        arr[project.id] = '已完结';
                    }
                    else {
                        arr[project.id] = '未完结;'
                    }
                }
                for (let i = 0; i < this.favorProjects.length; i++) {
                    let project = this.favorProjects[i];
                    if (project.end) {
                        arr[project.id] = '已完结';
                    }
                    else {
                        arr[project.id] = '未完结;'
                    }
                }
                for (let i = 0; i < this.donateProjects.length; i++) {
                    let project = this.donateProjects[i];
                    if (project.end) {
                        arr[project.id] = '已完结';
                    }
                    else {
                        arr[project.id] = '未完结;'
                    }
                }
                return arr;
            }
        },
        methods: {
            handleAvatarSuccess(res, file) {
                console.log(file);
            },
            // 上传前格式和图片大小限制
            async beforeAvatarUpload(file) {
                const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!type) {
                    this.$message.error('图片格式不正确!(只能包含jpg，png，webp，JPEG)')
                }
                if (!isLt2M) {
                    this.$message.error('上传图片大小不能超过 2MB!')
                }
                return type && isLt2M
            },

            uploadHttp({file}) {
                const fileName =  `avatar/${Date.parse(new Date())}`;
                ossClient(this.uploadConf).put(fileName, file, {
                    'ContentType': 'image/jpeg'
                }).then(({res, url, name}) => {
                    if (res && res.status === 200) {
                        console.log('更新成功', res, url, name);
                        this.imageUrl = url;
                        localStorage.setItem('touxiang',url);
                        localStorage.setItem('aliyun-oss', url);
                    }
                }).catch((err) => {
                    console.log('更新失败', err);
                });
            },

            async getUserInfo() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                App.getUserInfo().then(res => {
                    this.user.userAddress = res['userAddress'];
                    this.user.userName = res['username'];
                    this.user.time = res['time'];
                    this.user.phoneNumber = res['phoneNumber'];
                    this.user.index = res['index'];
                    this.user.idCard = res['idCard'];
                    this.user.email = res['email'];
                }).then(() => console.log('user info', this.user));
            },

            async getUserBalance() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                App.balanceOf().then(res => {
                    this.user.balance = res;
                });
            },

            async isExistUserAddress() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                App.isExistUserAddress().then(res => {
                    if (res) {
                        this.isExistUser = true;
                        let tmpUrl = localStorage.getItem('aliyun-oss');
                        console.log('avatar image is: ', tmpUrl);
                        if (tmpUrl) {
                            this.imageUrl = tmpUrl;
                        }
                        this.getUserInfo();
                        this.getUserBalance();
                        this.getOwnedProjects();
                        this.getFavorProjects();
                        this.getDonateProjects();
                    }
                });
            },

            async getOwnedProjects() {
                this.ownedProjects = [];
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getAllProjectNums().then(nums => {
                    nums.forEach(num => {
                        let tmp = {
                            'id': 0,
                            'tokenSum': 0,
                            'type': '',
                            'country': '',
                            'startDate': 0,
                            'end': null,
                            'projectName': '',
                            'projectDetail': '',
                            'projectType': '',
                            'owner': ''
                        };
                        tmp.id = num;
                        App.getProject(num).then(project => {
                            tmp.tokenSum = project[0];
                            tmp.type = project[1];
                            tmp.country = project[2];
                            tmp.startDate = project[3];
                            tmp.end = project[4];
                        });
                        App.getProjectDetail(num).then(project => {
                            tmp.projectName = project[0];
                            tmp.projectDetail = project[1];
                            tmp.projectType = project[2];
                            let ownerAddress = project[3];
                            App.findUser(ownerAddress).then(user => {
                                console.log(user['username']);
                                tmp.owner = user['username'];
                            });
                        });
                        this.ownedProjects.push(tmp);
                    });
                }).then(() => {
                    console.log('ownedProjects', this.ownedProjects);
                });

            },

            async getFavorProjects() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getAllFavorProjects().then(nums => {
                    nums.forEach(num => {
                        let tmp = {
                            'id': 0,
                            'tokenSum': 0,
                            'type': '',
                            'country': '',
                            'startDate': 0,
                            'end': false,
                            'projectName': '',
                            'projectDetail': '',
                            'projectType': '',
                            'owner': ''
                        };
                        tmp.id = num;
                        App.getProject(num).then(project => {
                            tmp.tokenSum = project[0];
                            tmp.type = project[1];
                            tmp.country = project[2];
                            tmp.startDate = project[3];
                            tmp.end = project[4];
                        });
                        App.getProjectDetail(num).then(project => {
                            tmp.projectName = project[0];
                            tmp.projectDetail = project[1];
                            tmp.projectType = project[2];
                            let ownerAddress = project[3];
                            App.findUser(ownerAddress).then(user => {
                                console.log(user['username']);
                                tmp.owner = user['username'];
                            });
                        });
                        this.favorProjects.push(tmp);
                    });
                }).then(() => {
                    console.log('favorProjects', this.favorProjects);
                });
            },

            async getDonateProjects() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getAllDonatedProjects().then(nums => {
                    nums.forEach(num => {
                        let tmp = {
                            'id': 0,
                            'tokenSum': 0,
                            'type': '',
                            'country': '',
                            'startDate': 0,
                            'end': false,
                            'projectName': '',
                            'projectDetail': '',
                            'projectType': '',
                            'owner': ''
                        };
                        tmp.id = num;
                        App.getProject(num).then(project => {
                            tmp.tokenSum = project[0];
                            tmp.type = project[1];
                            tmp.country = project[2];
                            tmp.startDate = project[3];
                            tmp.end = project[4];
                        });
                        App.getProjectDetail(num).then(project => {
                            tmp.projectName = project[0];
                            tmp.projectDetail = project[1];
                            tmp.projectType = project[2];
                            let ownerAddress = project[3];
                            App.findUser(ownerAddress).then(user => {
                                console.log(user['username']);
                                tmp.owner = user['username'];
                            });
                        });
                        this.donateProjects.push(tmp);
                    });
                }).then(() => {
                    console.log('donateProjects', this.donateProjects);
                });
            },

            tableRowClassName1(row) {
                if (row['end']) {
                    return 'success-row';
                }
                else {
                    return 'warning-row';
                }
            },
            formatDate(row, column, cellValue) {
                return new Date(cellValue * 1000).toDateString();
            },
            formatBoolean(row, column, cellValue) {
                let ret = '';
                if (cellValue) ret = '已完结';
                else ret = '未完结';
                return ret;
            },
            handleSelectionChange(val) {
                this.selectedProjects1 = val;
                console.log(val);
            },
            async endSelectedProjects() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                for (let index in this.selectedProjects1) {
                    App.endProject(this.selectedProjects1[index].id).then(res => {
                        console.log(res);
                        if (res) {
                            this.getOwnedProjects();
                            this.$notify({
                                title: '成功',
                                message: '项目完结成功',
                                type: 'success'
                            })
                        }
                        else {
                            this.$notify.error({
                                title: '失败',
                                message: '项目完结失败',
                            })
                        }
                    })
                }
            },
            click_item(row) {
                console.log('row is: ', row);
                this.$router.push({
                    path: "/prodetail",
                    query: {id:row.id},
                });
            }
        },
        created() {
            // let tmpUrl = localStorage.getItem('aliyun-oss');
            // console.log('avatar image is: ', tmpUrl);
            // this.imageUrl = tmpUrl;

            this.isExistUserAddress();
        },
    }
</script>

<style scoped>
    ::-webkit-scrollbar {
        display: none;
    }

    .mybody{
        background-size: cover;
        background: url("../img/center/center_bg.jpg") no-repeat fixed;
        width:100%;
        height:100%;/*宽高100%是为了图片铺满屏幕 */
        top: 0;
        z-index:-1;
        position: absolute;
        overflow: scroll;
    }

    .Avatar {
        margin-top: 100px;
        padding-left: 120px;
        height: 120px;
        /*width: 120px;*/
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }

    .avatar-uploader {
        left:3vw;
        border: 1px #d9d9d9;
        width: 120px;
        height: 120px;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 40px;
        color: #8c939d;
        width: 120px;
        height: 120px;
        line-height: 120px;
        text-align: center;
        /*left: 120px;*/
        /*position: absolute;*/
        /*background: #000000;*/
    }
    .avatar {
        width: 120px;
        height: 120px;
        display: block;

    }

    .info{
        position: absolute;
        top: 30vh;
        left:10vw;
        width: 80vw;
        height: 60vh;
        background-color: rgba(255,255,255,0.6);
    }
    .user-info-card {
        padding: 15px 20px 20px 20px;
    }

    .text {
        font-size: 17px;
    }

    .item {
        padding: 9px 0;
        text-align: left;
    }

    .el-table > .warning-row {
        background: oldlace;
    }

    .el-table > .success-row {
        background: #f0f9eb
    }

    .table-btn {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin: 10px 0 10px 10px;
    }
</style>

