<template>
    <div class="main-body" style="overflow:hidden;">
        <div class="topbar-hands">
            <transition  name="fade" >
                <img v-show="hello" id="hand1" src="../img/shou13.png" >
            </transition>
            <transition  name="fade1" >
                <img v-show="hello1" id="hand2" src="../img/shou12.png" >
            </transition>
            <transition  name="fade2" >
                <img v-show="hello2" id="hand3" src="../img/shou10.png" >
            </transition>
            <transition  name="fade3" >
                <img v-show="hello3" id="hand4" src="../img/shou16.png" >
            </transition>
        </div>
        <div id="topbar">
            <img src="../img/title.png"  id="title"/>
        </div>
        <div id="top2" style="color:darkgray;">
            <a id="en" style="font-size:20px" @click="enIsClicked">Entertainment </a>
            <a id="art" style="font-size:20px" @click="artIsClicked">Art </a>
            <a id="design" style="font-size:20px" @click="designIsClicked">Design </a>
            <a id="music" style="font-size:20px" @click="musicIsClicked">Music</a>
            <a id="edu" style="font-size:20px" @click="eduIsClicked">Education </a>
        </div>
        <div id="top3">
            <div id="userinfo">{{welcomeMessage}}</div>
            <div id="top3-right">
                <div class="glyphicon glyphicon-plus-sign" id="createButton" @click="createIsClicked">

                </div>
                <button class="btn btn-default" id="login" v-show="!isUser">
                    <router-link to="/register">Register</router-link>
                </button>
                <el-image class="avatar" id="avatarID" :src="avatarImg" @click="toUserPage($event.target.value)" v-show="isUser"/>

                <input type="text" id="search"  placeholder="Search hear..." name="search" value=""/>
                <router-link to="/searchrst">
                    <div class="glyphicon glyphicon-search" id="mygly">
                    </div>
                </router-link>
            </div>
            <!--
            <button  id="regis" class="btn btn-default">
              <router-link to="/register">Register</router-link>
            </button>
            -->
        </div>

        <!--轮播图-->
        <div v-show="lunboPart">
            <el-carousel :interval="4000" type="card" height="200px" id="crouselme">
                <el-carousel-item v-for="(list,index) in Mlist" :key="index" >
                    <img id="cimg" alt="" :src="list.images" @click="click_item_lunbo(index)"/>
                </el-carousel-item>
            </el-carousel>
        </div>

        <div class="project-list" v-if="Types && hasProjects">
            <div class="project-card" v-for="(item,index) in typeList" v-bind:key="index">
                <div class="project-image">
                    <img class="image" :src="getImageUrl(item.id)"  alt="project image" />
                </div>
                <div class="project-description">
                    <div class="project-name">{{item.projectName}}</div>
                    <div class="project-owner">项目发起人：{{item.owner}} </div>
                    <div class="project-country">所在国家：{{item.country}}</div>
                    <div class="project-startDate">开始时间：{{new Date(item.startDate * 1000).toDateString()}} </div>
                    <div class="project-end" v-if="item.end">状态：已完结</div>
                    <div class="project-end" v-else>状态：未完结</div>
                    <div class="project-like">点赞数：{{donateNum[item.id]}}</div>
                    <div class="project-btn">
                        <div class="thumb" v-if="!hasDonate[item.id]">
                            <el-button type="primary" icon="el-icon-thumb" circle @click="donate(item.id, item.type)"></el-button>
                        </div>
                        <div class="thumb" v-else>
                            <el-button type="primary" icon="el-icon-thumb" @click="donate_drawback(item.id, item.type)">已点赞</el-button>
                        </div>
                        <div class="star" v-if="!favorProjects.includes(item.id)">
                            <el-button type="warning" icon="el-icon-star-off" circle @click="favor(item.id, item.type)"></el-button>
                        </div>
                        <div class="star" v-else>
                            <el-button type="warning" icon="el-icon-star-off" @click="favor_drawback(item.id)">已收藏</el-button>
                        </div>
                        <div class="more-info">
                            <el-button type="success" icon="el-icon-info" @click="click_item(item.id)">详情</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="no-project" v-if="Types && !hasProjects">
            暂无项目
        </div>
    </div>

  <!--  <div v-else>Loading...</div>-->
</template>

<script>
    import {App} from '../js/Test'
    import Web3 from "web3";

    //  import '../contact'
    export default {
        name: "home",
        // computed: mapGetters('drizzle', ['isDrizzleInitialized']),
        data(){
            return{
                h1s:false,
                isUser: false,

                typeList: [],
                entertainmentList: [],
                educationList: [],
                artList: [],
                designList: [],
                musicList: [],

                Mlist:[
                    {
                        id:1,
                        images:require("../img/lun1.jpg")
                    },
                    {
                        id:2,
                        images:require("../img/lun2.jpg")
                    },
                    {
                        id:3,
                        images:require("../img/lun3.jpg")
                    },
                    {
                        id:4,
                        images:require("../img/lun4.jpg")
                    }
                ],
                Types:false,
                hasProjects: false,
                lunboPart:true,
                hello:false,
                hello1:false,
                hello2:false,
                hello3:false,
                user: {
                    userAddress: null,
                    userName: null,
                    time: null,
                    index: null,
                    idCard: null,
                    phoneNumber: null,
                    email: null
                },
                avatarImg: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',

                favorProjects: [],
                hasDonate: [],
                donateNum: []
            }
        },
        computed: {
            welcomeMessage: function () {
                if (this.user.userName === null || this.user.userName === '') {
                    return 'Welcome to TokenGo!'
                }
                return 'Welcome, ' + this.user.userName + '!';
            }
        },
        methods: {
            createIsClicked() {
                this.$router.push("/createProject")
            },

            handleScroll() {
                this.hello = true;
                this.hello1 = true;
                this.hello2 = true;
                this.hello3 = true;
            },
            click_item(id) {
                console.log('id is: ', id);
                this.$router.push({
                    path: "/prodetail",
                    query: {id:id},
                });
            },
            click_item_lunbo(index){
                this.$router.push({
                    path:"/prodetail",
                    params:{id:this.typeList[index].id},
                })
            },

            enIsClicked() {
                this.typeList = this.entertainmentList;
                if(this.lunboPart) {
                    this.lunboPart = false;
                    this.Types = true;
                }
                this.hasProjects = this.typeList !== null && this.typeList.length !== 0;
            },
            eduIsClicked() {
                this.typeList = this.educationList;
                if (this.lunboPart) {
                    this.lunboPart = false;
                    this.Types = true;
                }
                this.hasProjects = this.typeList !== null && this.typeList.length !== 0;
            },
            artIsClicked() {
                this.typeList = this.artList;
                if (this.lunboPart) {
                    this.lunboPart = false;
                    this.Types = true;
                }
                this.hasProjects = this.typeList !== null && this.typeList.length !== 0;
            },
            designIsClicked() {
                this.typeList = this.designList;
                if (this.lunboPart) {
                    this.lunboPart = false;
                    this.Types = true;
                }
                this.hasProjects = this.typeList !== null && this.typeList.length !== 0;
            },
            musicIsClicked() {
                this.typeList = this.musicList;
                if (this.lunboPart) {
                    this.lunboPart = false;
                    this.Types = true;
                }
                this.hasProjects = this.typeList !== null && this.typeList.length !== 0;
            },

            toUserPage(name){
                this.$router.push({
                    path:'/center',
                    params:{
                        centerusername:name,
                    },
                    name:'toCenter',
                })
            },

            async getProjectEachType(type) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                let projects = [];
                App.getProjectEachType(type).then(nums => {
                    console.log(nums);
                    nums.forEach(num => {
                        let tmp = {
                            'id': 0,
                            'tokenSum': 0,
                            'type': '',
                            'country': '',
                            'startDate': 0,
                            'end': false,
                            'projectName': '',
                            'projectDetail': '',
                            'projectType': '',
                            'owner': '',
                            'hasDonate': false,
                        };
                        console.log(num);
                        tmp.id = num;
                        App.getProject(num).then(project => {
                            tmp.tokenSum = project[0];
                            tmp.type = project[1];
                            tmp.country = project[2];
                            tmp.startDate = project[3];
                            tmp.end = project[4];
                            this.donateNum[num] = project[0];
                        });
                        App.getProjectDetail(num).then(project => {
                            tmp.projectName = project[0];
                            tmp.projectDetail = project[1];
                            tmp.projectType = project[2];
                            let ownerAddress = project[3];
                            console.log('Address type:', typeof(ownerAddress));
                            App.findUser(ownerAddress).then(user => {
                                console.log(user['username']);
                                tmp.owner = user['username'];
                            });
                        });
                        App.hasDonated(num).then(res => {
                            tmp.hasDonate = !res;
                            this.hasDonate[num] = !res;
                        });
                        projects.push(tmp);
                    });
                }).then(() => {
                    switch(type) {
                        case 'entertainment':
                            this.entertainmentList = projects;
                            break;
                        case 'education':
                            this.educationList = projects;
                            break;
                        case 'art':
                            this.artList = projects;
                            break;
                        case 'design':
                            this.designList = projects;
                            break;
                        case 'music':
                            this.musicList = projects;
                            break;
                        default:
                            this.entertainmentList = projects;
                    }
                    console.log(projects);
                });
            },

            async determineIsUser() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.isExistUserAddress().then(res => {
                    console.log(res);
                    this.isUser = res;
                    if (res) {
                        this.getUserInfo();
                    }
                })
            },

            async getUserInfo() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                App.getUserInfo().then(res => {
                    this.user.userAddress = res['userAddress'];
                    this.user.userName = res['username'];
                    this.user.time = res['time'];
                    this.user.phoneNumber = res['phoneNumber'];
                    this.user.index = res['index'];
                    this.user.idCard = res['idCard'];
                    this.user.email = res['email'];
                }).then(() => console.log('user info', this.user));
            },

            async getFavorProjects() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.getAllFavorProjects().then(res => {
                    this.favorProjects = res;
                })
            },

            async updateTypeList(type) {
                switch (type) {
                    case 'entertainment':
                        this.getProjectEachType('entertainment').then(() => {
                            this.typeList = this.entertainmentList;
                        });
                        break;
                    case 'education':
                        this.getProjectEachType('education').then(() => {
                            this.typeList = this.educationList;
                        });
                        break;
                    case 'art':
                        this.getProjectEachType('art').then(() => {
                            this.typeList = this.artList;
                        });
                        break;
                    case 'design':
                        this.getProjectEachType('design').then(() => {
                            this.typeList = this.designList;
                        });
                        break;
                    case 'music':
                        this.getProjectEachType('music').then(() => {
                            this.typeList = this.musicList;
                        });
                        break;
                    default:
                        this.getProjectEachType('entertainment').then(() => {
                            this.typeList = this.entertainmentList;
                        });
                }
            },

            async donate(id, type) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.donate(id).then(res => {
                    console.log(res);
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '点赞成功',
                            type: 'success'
                        });
                        this.updateTypeList(type);
                        this.hasDonate.splice(id, 1, true);
                        let now = (parseInt(this.donateNum[id]) + 1).toString();
                        this.donateNum.splice(id, 1, now);
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '点赞失败'
                        })
                    }
                });
            },

            async donate_drawback(id, type) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.donateDrawBack(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '取消点赞成功',
                            type: 'success'
                        });
                        this.updateTypeList(type);
                        this.hasDonate.splice(id, 1, false);
                        let now = (parseInt(this.donateNum[id]) - 1).toString();
                        this.donateNum.splice(id, 1, now);
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '取消点赞失败'
                        })
                    }
                });
            },

            async favor(id) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.addFavorProject(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '收藏成功',
                            type: 'success'
                        });
                        this.getFavorProjects();
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '收藏失败'
                        })
                    }
                })
            },

            async favor_drawback(id) {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.deleteFavorProject(id).then(res => {
                    if (res) {
                        this.$notify({
                            title: '成功',
                            message: '取消收藏成功',
                            type: 'success'
                        });
                        this.getFavorProjects();
                    }
                    else {
                        this.$notify.error({
                            title: '失败',
                            message: '取消收藏失败'
                        })
                    }
                })
            },

            getImageUrl(id) {
                let url = 'https://citi-cup.oss-cn-beijing.aliyuncs.com/project-img/' + id + '.jpg';
                console.log(url);
                return url;
            }
        },
        created() {

            this.determineIsUser();

            let tmpUrl = localStorage.getItem('aliyun-oss');
            console.log(tmpUrl);
            if (tmpUrl) {
                this.avatarImg = tmpUrl;
            }

            this.getProjectEachType('entertainment');
            this.getProjectEachType('education');
            this.getProjectEachType('art');
            this.getProjectEachType('design');
            this.getProjectEachType('music');

            this.getFavorProjects();
        },

        mounted() {

            window.addEventListener('mousewheel',this.handleScroll);

        },

    }
</script>

<style scoped>
    .topbar-hands{
        width:100%;
        overflow: hidden;
        /*position:absolute;*/
        top:0px;
    }
    #topbar{
        position: absolute;
        top:40vh;
        /*top: 169px;*/
        border-bottom-style: solid;
        border-color: lightgray;
        border-width:1px;
        width:100%;
        height:38vh;
        flex-direction: row;
    }
    #title{width:30vw;height:100%;}


    #cimg{float: left;margin-left: 15%;}

    #lixiang{
        font-size: 17px;
        margin-left:3vw;
        height:8vh;
        margin-bottom:1vh;
    }
    #lixiang:hover{
        cursor: pointer;
        color:darkgrey;
    }
    /*轮播图*/
    #crouselme{position: absolute;overflow:hidden;top:110vh;width: 100%;}

    /*top2*/
    #top2{
        overflow: hidden;
        flex-direction: row;
        height:10vh;
        border-bottom-style: solid;
        border-color: lightgray;
        border-width:1px;
        position: absolute;
        top:79vh;
        width:100%;


    }
    #edu{flex:1;line-height: 10vh;font-size: 17px;color:darkgray;margin-left: 3vw;}
    #music{flex:1;line-height: 10vh;font-size: 17px;color:darkgray;margin-left: 3vw;}
    #design{flex:1;line-height: 10vh;font-size: 17px;color:darkgray;margin-left: 3vw;}
    #art{flex:1;line-height: 10vh;font-size: 17px;color:darkgray;margin-left: 3vw;}
    #en{flex:1;line-height: 10vh;font-size: 17px;color:darkgray;}
