    #edu:hover{cursor: pointer;color:black;}
    #music:hover{cursor: pointer;color:black;}
    #design:hover{cursor: pointer;color: #000000;}
    #art:hover{cursor: pointer;color:black;}
    /*top3*/
    #top3{
        display: flex;
        flex-direction: row;
        height:10vh;
        background-color: rgba(0,0,0,0.8);
        position: fixed;
