        cursor: pointer;
    }

    #types{
        position: absolute;left:10vw;top:110vh;width:80vw ;height:80vh;
    }
    /*
    #regis{margin-left:1vw;margin-top:2vh;height:6vh;line-height:4vh;font-size: 17px;color: #a9a9a9;}
    #regis a{color:black;}
