        margin-left:15px;
        height:6vh;
        font-size: 17px;
        line-height:4vh;
        color:darkgray;
    }
    #login a{color:black;}
    #login:hover a{color:white;}
    #login:hover{background-color: black;color:white;}
    #userinfo{
        margin-left:1vw;
        /*margin-top:2vh;*/
        /*width:6vw;*/
        font-size: 20px;
        height:6vh;
        line-height: 6vh;
        color:white;
    }

    #search{
        margin-left:15px;
        margin-right:15px;
        height:6vh;
        font-size: 17px;
        border-color:darkgray;
        border-radius: 5px;
        border-style:solid;
        border-width:1px;
        text-indent: 15px;
    }
    #mygly{
        margin-right:15px;
        /*margin-left:15px;*/
        /*height:40px;*/
    }
    #createButton{
        /*margin-right: 15px;*/
        margin-left:15px;
        /*height:6vh;*/
    }
    #createButton:hover{cursor: pointer;}

    #hand1{margin-left: 0;float:left;margin-top:100px;width:20%;height:10%;}
    #hand2{width:35%;height:25%;margin-left: -100px;margin-top:0;}
    #hand3{width:20%;height:10%;margin-left: 0;margin-top:-100px;}
    #hand4{width:20%;height:10%;margin-right:0;float:right;margin-top:100px;}
    body{
        margin:0;
        overflow: hidden;
    }


    .el-carousel__item h3 {
        color: #475669;
        font-size: 14px;
        opacity: 0.75;
        line-height: 200px;
        margin: 0;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
    .fade1-enter-active, .fade1-leave-active {
        transition: opacity .5s;
    }
    .fade1-enter, .fade1-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
    .fade2-enter-active, .fade2-leave-active {
        transition: opacity .5s;
    }
    .fade2-enter, .fade2-leave-to /* .fade-leave-active below version 2.1.8 */ {
