<template>
    <div id="mytopbar">
        <div id="userinfo">{{welcomeMessage}}</div>
        <div id="top3-right">
            <button class="btn btn-default" id="login" v-show="!isUser">
                <router-link to="/register">Register</router-link>
            </button>
            <el-image class="avatar" id="avatarID" :src="avatarImg" @click="toUserPage($event.target.value)" v-show="isUser"/>
            <div id="home-button">
                <el-button type="primary" icon="el-icon-s-home" circle plain @click="toHome" />
            </div>
        </div>
    </div>
</template>

<script>
    import {App} from "../js/Test";
    import Web3 from "web3";

    export default {
        name: "topbar",
        data(){
            return{
                avatarImg: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
                user: {
                    userAddress: null,
                    userName: null,
                    time: null,
                    index: null,
                    idCard: null,
                    phoneNumber: null,
                    email: null
                },
                isUser: false
            }
        },
        computed: {
            welcomeMessage: function () {
                if (this.user.userName === null || this.user.userName === '') {
                    return 'Welcome to TokenGo!'
                }
                return 'Welcome, ' + this.user.userName + '!';
            }
        },
        methods:{
            async determineIsUser() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }

                App.isExistUserAddress().then(res => {
                    console.log(res);
                    this.isUser = res;
                    if (res) {
                        this.getUserInfo();
                    }
                })
            },

            async getUserInfo() {
                if (window.ethereum) {
                    // use MetaMask's provider
                    App.web3 = new Web3(window.ethereum);
                    window.ethereum.enable(); // get permission to access accounts
                } else {
                    console.warn(
                        "No web3 detected. Falling back to http://127.0.0.1:8545. You should remove this fallback when you deploy live",
                    );
                    App.web3 = new Web3(
                        new Web3.providers.HttpProvider("http://127.0.0.1:8545")
                    );
                }
                App.getUserInfo().then(res => {
                    this.user.userAddress = res['userAddress'];
                    this.user.userName = res['username'];
                    this.user.time = res['time'];
                    this.user.phoneNumber = res['phoneNumber'];
                    this.user.index = res['index'];
                    this.user.idCard = res['idCard'];
                    this.user.email = res['email'];
                }).then(() => console.log('user info', this.user));
            },

            toHome() {
                this.$router.push({
                    path: '/home'
                })
            },

            toUserPage(name){
                this.$router.push({
                    path:'/center',
                    params:{
                        centerusername:name,
                    },
                    name:'toCenter',
                })
            },
        },
        created() {
            this.determineIsUser();

            let tmpUrl = localStorage.getItem('aliyun-oss');
            console.log(tmpUrl);
            if (tmpUrl) {
                this.avatarImg = tmpUrl;
            }
        }
    }
</script>

<style scoped>
    #mytopbar{
        display: flex;
        flex-direction: row;
        height:10vh;
        background-color: rgba(0,0,0,0.8);
        position: fixed;
        top:0;
        width:100%;
        justify-content: space-between;
        align-items: center;
        z-index: 999;
    }
    #userinfo{
        margin-left:1vw;
        /*margin-top:2vh;*/
        /*width:6vw;*/
        font-size: 20px;
        height:6vh;
        line-height: 6vh;
        color:white;
    }
    #top3-right {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }
    #login{
        margin-left:15px;
        margin-right: 30px;
        height:6vh;
        font-size: 17px;
        line-height:4vh;
        color:darkgray;
    }
    #login a{color:black;}
    #login:hover a{color:white;}
    #login:hover{background-color: black;color:white;}
    .avatar {
        height: 45px;
        width: 45px;
        display: block;
        border-radius: 6px;
        margin-left: 30px;
        margin-right: 30px;
    }

    #avatarID:hover {
        cursor: pointer;
    }

    #home-button {
        margin-right: 30px;
    }
</style>
